import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../components/home'
import DefaultHeadersAndProxy from '../components/config/defaultHeadersAndProxy'
import SpidersAndMiddlewares from '../components/config/spidersAndMiddlewares'
import OthersSetting from '../components/config/othersSetting'
import Dashboard from '../components/dashboard'

Vue.use(VueRouter)

const routes = [{
  path: '/',
  component: home,
  children: [{
    path: 'dashboard',
    component: Dashboard
  }, {
    path: '/defaultHeadersAndProxy',
    component: DefaultHeadersAndProxy
  }, {
    path: '/spidersAndMiddlewares',
    component: SpidersAndMiddlewares
  }, {
    path: '/othersSetting',
    component: OthersSetting
  }]
}]

const router = new VueRouter({
  routes
})

export default router
