<template>
  <el-container>
    <el-header>
      <span><i class="iconfont iconall"></i></span>
      <h2>Jscrapy Admin</h2>
      <span></span>
    </el-header>
    <el-container>
      <el-aside :width="asideWidth[isCollapse? 0:1]+'px'">
        <div class="toggle-button" @click="isCollapse=!isCollapse">|||</div>
        <el-menu
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="navState"
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409eff">
          <el-menu-item @click="saveNavState(item.path)" v-for="item in getMenuList(true)" :key="item.id" :index="item.path">
            <template>
              <i class="el-icon-menu"></i>
              <span>{{item.name}}</span>
            </template>
          </el-menu-item>

          <el-submenu v-for="item in getMenuList(false)" :key="item.id" :index="item.path">
            <template slot="title">
              <i class="el-icon-setting"></i>
              <span>{{item.name}}</span>
            </template>
            <el-menu-item @click="saveNavState(childItem.path)" v-for="childItem in item.children" :key="childItem.id" :index="childItem.path">
              <template slot="title">
                <span>{{childItem.name}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main><router-view></router-view></el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      menuList: [{
        id: 1,
        name: '界面',
        path: '/dashboard',
        children: []
      }, {
        id: 2,
        name: '配置文件',
        path: '/config',
        children: [{
          id: 21,
          name: '默认请求头以及代理',
          path: '/defaultHeadersAndProxy',
          children: []
        }, {
          id: 22,
          name: '爬虫以及中间件',
          path: '/spidersAndMiddlewares',
          children: []
        }, {
          id: 23,
          name: '其他配置',
          path: '/othersSetting',
          children: []
        }]
      }],
      isCollapse: false,
      asideWidth: [64, 200],
      navState: ''
    }
  },
  methods: {
    saveNavState (url) {
      this.navState = url
      window.sessionStorage.setItem('navState', url)
    },
    getMenuList (single) {
      return this.menuList.filter(obj => (obj.children.length === 0) === single)
    }
  },
  computed: {
  },
  async created () {
    this.navState = window.sessionStorage.getItem('navState')
  }
}
</script>

