<template>
  <div>
    <el-card :key="'outside'+index" v-for="(item, index) in config">
      <div slot="header">
        <span>{{item.name}}</span>
        <el-tooltip class="item" effect="dark" :content="item.description" placement="top">
          <i class="el-icon-info"></i>
        </el-tooltip>
        <el-button size="small" type="primary" @click="item.value.push({
          name: '',
          description: '',
          key: '',
          value: 50
        })">添加</el-button>
      </div>
      <draggable draggable=".configRow">
        <el-row :gutter="15" :key="'inside'+index2" v-for="(obj,index2) in item.value" class="configRow">
