          </el-col>
          <el-col :span="1">:</el-col>
          <el-col class="keyClass" :span="2">
            <el-input v-model.number="obj.value"></el-input>
          </el-col>
          <el-col :span="1">
            <i class="el-icon-error" style="color: red; cursor: pointer"
               @click="item.value.splice(index2,1)"></i>
          </el-col>
        </el-row>
      </draggable>
    </el-card>
  </div>
