<script>
import draggable from 'vuedraggable'
import Vue from 'vue'

export default {
  name: 'spidersAndMiddlewares',
  components: {
    // eslint-disable-next-line vue/no-unused-components
    draggable
  },
  created () {
    this.getConfig()
  },
  data () {
    return {
      config: [{
        name: '爬虫对象',
        description: '系统启动时会加载这些爬虫对象',
        key: 'spiders',
        value: [{
          name: '',
          description: '',
          key: 'aca',
          value: 1
        }]
      }, {
        name: '爬虫中间件',
        description: '处理进入爬虫的响应和爬虫产生的请求, 数值越小优先级越高',
        key: 'spiderMiddlewares',
        value: [{
          name: '',
          description: '',
          key: 'me.tianjx98.Jscrapy.middleware.spider.impl.DepthSpiderMiddleware',
          value: 1
        }, {
          name: '',
          description: '',
          key: 'me.tianjx98.Jscrapy.middleware.spider.impl.AllowedDomainSpiderMiddleware',
          value: 2
        }]
      }, {
