          key: 'test.scraper.novel.NovelPipeline',
          value: 1
        }]
      }]
    }
  },
  methods: {
    async getConfig () {
      const { data: res } = await this.$http.get('/config/spidersAndMiddlewares')
      this.config = res
    },
    deleteKeyValue (obj, key) {
      Vue.delete(obj, key)
    }
  },
  mounted () {
  },
  destroyed () {
    this.$http.put('/config/update', { config: this.config })
  }
}
</script>

