<template>
  <div>
    <el-card>
      <div slot="header">
        <span>{{config[0].name}}</span>
        <el-tooltip class="item" effect="dark" :content="config[0].description" placement="top">
          <i class="el-icon-info"></i>
        </el-tooltip>
        <el-button size="small" type="primary" @click="dialogFormVisible=true">添加</el-button>
      </div>
      <el-row :gutter="15" :key="index" v-for="(obj,index) in config[0].value">
        <el-col :span="5">
          <el-input v-model="obj.key"></el-input>
        </el-col>
        <el-col :span="1">:</el-col>
        <el-col :span="17">
          <el-input v-model="obj.value"></el-input>
        </el-col>
        <el-col :span="1"><i class="el-icon-error" style="color: red; cursor: pointer"
                             @click="config[0].value.splice(index,1)"></i></el-col>
      </el-row>
    </el-card>
    <el-card>
      <div style="display: flex; justify-content: space-between" slot="header">
        <span>{{config[1].name}}
          <el-tooltip class="item" effect="dark" :content="config[1].description" placement="top">
          <i class="el-icon-info"></i>
        </el-tooltip>
        </span>

        <el-switch
          v-model="config[1].value[0].value"
          active-color="#13ce66"
          inactive-color="gray">
        </el-switch>
      </div>
      <el-row :gutter="15">
        <el-col class="keyClass" :span="2">
          <div>{{config[1].value[1].key}}</div>
        </el-col>
        <el-col :span="1">:</el-col>
        <el-col :span="21">
          <el-input v-model="config[1].value[1].value"></el-input>
        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col class="keyClass" :span="2">
          <div>{{config[1].value[2].key}}:</div>
        </el-col>
        <el-col :span="1">:</el-col>
        <el-col :span="21">
          <el-input v-model.number="config[1].value[2].value"></el-input>
        </el-col>
      </el-row>
    </el-card>

    <el-dialog title="添加默认请求头" :visible.sync="dialogFormVisible">
      <el-row :gutter="15">
        <el-col class="keyClass" :span="3">
          <div>Header :</div>
        </el-col>
        <el-col :span="20">
          <el-input v-model="newHeader"></el-input>
        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col class="keyClass" :span="3">
          <div>Value :</div>
        </el-col>
        <el-col :span="20">
          <el-input v-model="newValue"></el-input>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addDefaultHeader">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'defaultHeadersAndProxy',
  created () {
    this.getConfig()
  },
  data () {
    return {
      config: [{
        name: '默认请求头',
        description: '每次发送请求时都会添加这些请求头',
        key: 'defaultHeaders',
        value: [{
          name: '',
          description: '',
          key: 'Accept',
          value: '*/*'
        }, {
          name: '',
          description: '',
          key: 'Accept-Language',
          value: 'zh-CN,zh;q=0.8,en-US;q=0.5,en;q=0.3'
        }]
      }, {
        name: '代理',
        description: '每次发送请求都会经过代理',
        key: 'proxy',
        value: [{
          name: '',
          description: '',
          key: 'enable',
          value: false
        }, {
          name: '',
          description: '',
          key: 'host',
          value: ''
        }, {
          name: '',
          description: '',
          key: 'port',
          value: 8888
        }]
      }],
      dialogFormVisible: false,
      newHeader: '',
      newValue: ''
    }
  },
  methods: {
    async getConfig () {
      const { data: res } = await this.$http.get('/config/defaultHeadersAndProxy')
      if (res.length === 1) {
        res.push({
          name: '代理',
          description: '每次发送请求都会经过代理',
          key: 'proxy',
          value: [{
            name: '',
            description: '',
            key: 'host',
            value: ''
          }, {
            name: '',
            description: '',
            key: 'port',
            value: ''
          }]
        })
      }
      this.config = res
    },
    deleteKeyValue (obj, key) {
      Vue.delete(obj, key)
    },
    setKeyValue (obj, key, val) {
      Vue.set(obj, key, val)
    },
    addDefaultHeader () {
      Vue.set(this.config.value[0].data, this.newHeader, this.newValue)
      this.dialogFormVisible = false
      this.newHeader = ''
      this.newValue = ''
    }
  },
  computed: {},
  destroyed () {
    this.$http.put('/config/update', { config: this.config })
  }
}
</script>

<style scoped>
  .el-row {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }

  .el-card {
    margin-bottom: 15px;
  }

  .keyClass {
    text-align: right;
  }

  .el-button {
    margin-left: 15px;
  }

  .el-icon-info {
    margin-left: 8px;
    opacity: 0.4;
  }
</style>
