import Vue from 'vue'
// eslint-disable-next-line no-unused-vars
import { Button, Container, Header, Aside, Main, Menu, Submenu, MenuItem, Row, Col, Card, Input, Dialog, Tooltip, InfiniteScroll, Switch, Tree, Pagination, Message, MessageBox, Radio } from 'element-ui'

const components = [Button, Container, Header, Aside, Main, Menu, Submenu, MenuItem, Row, Col, Card, Input, Dialog, Tooltip, InfiniteScroll, Switch, Tree, Pagination, Message, MessageBox, Radio]

components.forEach((item) => {
  Vue.component(item.name, item)
})
Vue.prototype.$message = Message
Vue.prototype.$messageBox = MessageBox

