<template>
  <div>
    <el-row class="outsideRow" :gutter="20">
      <el-col>
        <el-card>
          <div slot="header">
            <span>其他配置</span>
          </div>
          <el-row :gutter="15" :key="index" v-for="(obj,index) in config">
            <el-col class="keyClass" :span="12">
              <div>{{obj.name}}:</div>
            </el-col>
            <el-tooltip class="item" effect="dark" :content="obj.description" placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
            <el-col :span="6">
              <el-input v-model.number="obj.value"></el-input>
            </el-col>
          </el-row>
        </el-card>
      </el-col>

      <el-col style="margin-top: 0">
        <el-card style="height: 100%">
          <div slot="header">
            <span>{{scheduler.name}}</span>
          </div>
          <el-row :gutter="15">
            <el-col class="keyClass" :span="5">
              <div>{{scheduler.value[0].name}}:</div>
            </el-col>
            <el-tooltip class="item" effect="dark" :content="scheduler.value[0].description" placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
            <el-col :span="18">
              <el-input v-model="scheduler.value[0].value"></el-input>
            </el-col>
          </el-row>
          <el-row :gutter="15">
            <el-col class="keyClass" :span="5">
              <div>{{scheduler.value[1].name}}:</div>
            </el-col>
            <el-tooltip class="item" effect="dark" :content="scheduler.value[1].description" placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
            <el-col :span="18">
              <el-radio v-model="scheduler.value[1].value" :label="true">广度优先</el-radio>
              <el-radio v-model="scheduler.value[1].value" :label="false">深度优先</el-radio>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'othersSetting',
  created () {
    this.getConfig()
  },
  data () {
    return {
      config: [{
        name: '最大线程数',
        description: '同时工作的线程数量',
        key: 'maxThreadNum',
        value: 4
      }, {
        name: '请求超时时间',
        description: '如果发出请求后超出此时间还未收到响应就取消此次请求,单位为s',
        key: 'connectionTimeout',
        value: 10
      }, {
        name: '并发请求的最大值',
        description: '同一时间允许发出请求的最大数量,默认最多可以同时发出16个请求',
        key: 'concurrentRequests',
        value: 16
      }, {
        name: '每一个域名下的并发请求最大值',
        description: '同一域名下同一时间允许发出请求的最大数量,默认最多可以同时发出8个请求',
        key: 'concurrentRequestsPerDomain',
        value: 8
      }, {
        name: '随机下载延迟',
        description: '随机下载延迟, 对于同一个域名下的请求, 两次请求之间会产生一个0-randomDownloadDelay(s)的延迟',
        key: 'randomDownloadDelay',
        value: 0
      }],
      scheduler: {
        name: '调度器配置',
        description: '',
        key: 'scheduler',
        value: [{
          name: 'URL去重类',
          description: '用于实现调度器中的去重算法,默认使用 布隆过滤器',
          key: 'defaultDupFilter',
          value: 'me.tianjx98.Jscrapy.duplicatefilter.impl.BloomDuplicateFilter'
        }, {
          name: '爬取规则',
          description: '默认为（true）广度优先爬取，设置为false则是深度优先爬取',
          key: 'bfs',
          value: true
        }]
      },
      dialogFormVisible: false,
      newHeader: '',
      newValue: ''
    }
  },
  methods: {
    async getConfig () {
      const { data: res } = await this.$http.get('/config/others')
      this.config = res
      const { data: scheduler } = await this.$http.get('/config/scheduler')
      this.scheduler = scheduler
    },
    deleteKeyValue (obj, key) {
      Vue.delete(obj, key)
    },
    setKeyValue (obj, key, val) {
      Vue.set(obj, key, val)
    },
    addDefaultHeader () {
      Vue.set(this.config[0].data, this.newHeader, this.newValue)
      this.dialogFormVisible = false
      this.newHeader = ''
      this.newValue = ''
    }
  },
  destroyed () {
    this.$http.put('/config/update', { config: this.config })
    this.$http.put('/config/update', { config: this.scheduler })
  }
}
</script>

