<template>
  <div>
    <el-card>
      <div slot="header">
        <span>引擎</span>
        <span style="margin-left: 20px;margin-right: 20px;">{{engineStatus}}</span>
        <el-button @click="startEngine" type="primary">启动</el-button>
        <el-button @click="pauseEngine" type="warning" :disabled="closed">暂停</el-button>
        <el-button @click="closeEngine" type="danger" :disabled="closed">关闭</el-button>
      </div>
      <el-row :gutter="15" type="flex">
        <el-col :span="12">
          <el-card>
            <div slot="header" style="display: flex; justify-content: space-between">
              <span>Scheduler</span>
              <span>总请求数: {{schedulerInfo.totalRequestNum}}</span>
              <span>当前请求数: {{schedulerInfo.currentRequestNum}}</span>
              <span>重复请求: {{schedulerInfo.repeatRequestNum}}</span>
            </div>
            <div class="box">
              <div style="overflow: auto; height:100%">
                <el-tree :data="schedulerInfo.data" :props="defaultProps"></el-tree>
              </div>
            </div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentPageChange"
              :current-page="page"
              :page-sizes="[5, 10, 15]"
              :small="true"
              :page-size="pageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="schedulerInfo.currentRequestNum">
            </el-pagination>
          </el-card>
        </el-col>

        <el-col :span="12">
          <el-card>
            <div slot="header" style="display: flex; justify-content: space-between">
              <span>Downloader</span>
              <span>已发送请求数: {{downloaderInfo.totalDownloadRequests}}</span>
              <span>当前请求数: {{downloaderInfo.downloadingRequests}}</span>
            </div>
            <div class="box">
              <el-tree :data="downloaderInfo.data" :props="defaultProps"></el-tree>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'dashboard',
  data () {
    return {
      closed: true,
      paused: false,
      page: 1,
      pageSize: 5,
      busy: false,
      schedulerInfo: {
        totalRequestNum: 0,
        repeatRequestNum: 0,
        currentRequestNum: 0,
        data: []
      },
      downloaderInfo: {
        downloadingRequests: 0,
        totalDownloadRequests: 0,
        data: []
      },
      defaultProps: {
        label: 'label',
        children: 'children'
      }
    }
  },
  created () {
    this.loadEngineInfo()
    setInterval(this.loadDownloaderInfo, 3000)
    setInterval(this.loadSchedulerInfo, 3000)
  },
  methods: {
    async loadEngineInfo () {
      const { data: res } = await this.$http.get('/engine/info')
      this.closed = res.closed
      this.paused = res.paused
    },
    async loadSchedulerInfo () {
      if (this.closed) return
      const { data: res } = await this.$http.get(`/engine/scheduler/${this.page}/${this.pageSize}`)
      this.schedulerInfo.totalRequestNum = res.totalRequestNum
      this.schedulerInfo.repeatRequestNum = res.repeatRequestNum
      this.schedulerInfo.currentRequestNum = res.currentRequestNum
      this.schedulerInfo.data.splice(0, this.schedulerInfo.data.length)
      this.schedulerInfo.data.push.apply(this.schedulerInfo.data, this.formatList(res.data))
    },
    async loadDownloaderInfo () {
      if (this.closed) return
      const { data: res } = await this.$http.get('/engine/downloader')
      this.downloaderInfo.downloadingRequests = res.downloadingRequests
      this.downloaderInfo.totalDownloadRequests = res.totalDownloadRequests
      let c = 0
      for (const key in res.data) {
        const a = this.formatList(res.data[key])
        if (this.downloaderInfo.data.length === c) {
          this.downloaderInfo.data.push({
            label: '',
            children: []
          })
        }
        this.downloaderInfo.data[c].label = key
        this.downloaderInfo.data[c].children.splice(0, this.downloaderInfo.data[c].children.length)
        this.downloaderInfo.data[c].children.push.apply(this.downloaderInfo.data[c].children, a)
        c++
      }
    },
    formatList (res) {
      const result = []
      if (res === null) return result
      for (const item of res) {
        result.push(this.formatObj(item))
      }
      return result
    },
    formatObj (obj) {
      const node = { children: [] }
      for (const key in obj) {
        if (key === 'url') {
          node.label = obj[key]
        } else {
          node.children.push(this.convertKeyValue(key, obj[key]))
        }
      }
      return node
    },
    convertKeyValue (key, value) {
      const node = { children: [] }
      if (value instanceof Array) {
        node.label = key
        for (const item of value) {
          node.children.push({
            label: item
          })
        }
        return node
      } else if (value instanceof Object) {
        const newNode = this.formatObj(value)
        newNode.label = key
        return newNode
      } else {
        node.label = key + ': ' + value
        return node
      }
    },
    async startEngine () {
      if (!this.closed) { // 如果引擎已经启动
        // 弹出提示, 是否重启
        this.$messageBox.confirm('是否重启引擎?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          await this.$http.get('/engine/control/start')
          this.loadEngineInfo()
          this.$message({
            type: 'success',
            message: '重启引擎成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消重启'
          })
        })
      } else {
        await this.$http.get('/engine/control/start')
        this.$message.success('爬虫引擎启动成功!')
        this.loadEngineInfo()
      }
    },
    async pauseEngine () {
      this.paused = !this.paused
      await this.$http.get('/engine/control/pause')
      this.loadSchedulerInfo()
      this.loadDownloaderInfo()
      this.$message.success('引擎已' + (this.paused ? '暂停' : '启动') + '!')
    },
    closeEngine () {
      this.$messageBox.confirm('是否关闭引擎?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.$http.get('/engine/control/close')
        await this.loadEngineInfo()
        this.$message({
          type: 'success',
          message: '引擎关闭成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消关闭'
        })
      })
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.loadSchedulerInfo()
    },
    handleCurrentPageChange (val) {
      this.page = val
      this.loadSchedulerInfo()
    }
  },
  computed: {
    engineStatus () {
      let msg = ''
      if (this.closed) {
        msg = '未启动...'
      } else {
        if (this.paused) {
          msg = '暂停中...'
        } else {
          msg = '运行中...'
        }
      }
      return msg
    }
  }
}
</script>

<style scoped>
  .box {
    height: 230px;
  }
  .el-card{
    height: 100%;
  }
</style>
